.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 100%;

  grid-template-areas:
    "h h"
    "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
}

.app-wrapper-content {
  grid-area: c;
  background-color: rgb(248, 187, 243);
}

a.active-link {
  color: rgb(255, 38, 0);
}

a.inactive-link {
  color: rgb(30, 143, 98);
}
